@import url(https://fonts.googleapis.com/css?family=Kaushan+Script);
:root {
  --mainPink: #e62b5c;
  --mainYellow: rgb(249, 228, 148);
  --mainWhite: #fff;
  --mainBlack: #000;
  --yellowTrans: rgba(249, 228, 148, 0.5);
  --mainGrey: rgb(238, 238, 238);
}
html,
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Kaushan Script', cursive;
  background: #fff;
  background: var(--mainWhite);
  color: #000;
  color: var(--mainBlack);
}
a {
  text-decoration: none;
}
.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-bottom: 0.5rem;
  font-family: inherit;
  font-weight: 500;
  line-height: 1.2;
  color: inherit;
}
h1 {
  font-size: 2.5rem;
}
.btn {
  display: inline-block;
  font-weight: 400;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  border: 1px solid transparent;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  line-height: 1.5;
  text-transform: uppercase;
  text-decoration: none;
  border-radius: 0.25rem;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
    border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}
@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}

@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}

@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}
@media (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
}
.container {
  width: auto;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

/* banner */

.max-height {
  min-height: calc(100vh - 76px);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
  position: relative;
  display: grid;
  place-items: center;
  margin-top: 67px;
}
.banner {
  color: #fff;
  color: var(--mainWhite);
  margin-top: -4rem;
  text-align: center;
  text-transform: capitalize;
}
.banner-title {
  color: #e62b5c;
  color: var(--mainPink);
  font-size: 4rem;
}
.banner-link {
  font-size: 1.5rem;
  color: #000;
  color: var(--mainBlack);
  border: 0.2rem solid #000;
  border: 0.2rem solid var(--mainBlack);
}
.banner-link:hover {
  background: #000;
  background: var(--mainBlack);
  color: #e62b5c;
  color: var(--mainPink);
}
/* endo of banner */
/* cart  */

.search-box {
  background: #e62b5c;
  background: var(--mainPink);
  color: #000;
  color: var(--mainBlack);
}

.back-to-top {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  position: fixed;
  bottom: 10px;
  right: 10px;
  color: #fff;
  color: var(--mainWhite);
  background-color: #e62b5c;
  background-color: var(--mainPink);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  font-weight: bolder;
  box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12) !important;
  cursor: pointer;
  z-index: 11;
}
.back-to-top i {
  margin: 0 !important;
}

/* ligthbox */

.lightbox-container {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
  background: rgba(0, 0, 0, 0.6) !important;
  align-items: center;
}

.lightbox-holder {
  position: relative;
  margin: 0 auto;
  max-width: 80%;
  text-align: right;
}
.lightbox-item {
  min-height: 80vh;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  border-radius: 0.3rem;
}
.lightbox-close {
  color: var(--mainPink);
  font-size: 3rem;
  transition: all 1s ease-in-out;
  cursor: pointer;
}
.lightbox-close:hover {
  color: var(--mainYellow);
}

.lightbox-control {
  position: absolute;
  font-size: 4rem;
  color: var(--mainPink);
  transition: all 1s linear;
  cursor: pointer;
}
.lightbox-control:hover {
  color: var(--mainYellow);
}
.btnLeft {
  top: 50%;
  left: 0;
  transform: translateX(-60%);
  display: none;
}
.btnRight {
  top: 50%;
  right: 0;
  transform: translateX(60%);
  display: none;
}

.store {
  background: var(--mainGrey);
  padding: 1em 0;
}
h5 {
  margin: 0;
}
.store-header {
  text-align: center;
}
.store-items {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-gap: 1.5em;
}
.store-item {
  background-color: var(--mainWhite);
  box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12) !important;
}
.store-items a {
  color: var(--mainBlack);
}
.sortBtn {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  margin: 0 auto;
  max-width: 100%;
}
.filter-btn {
  margin: 0.5rem;
}
.item-text {
  text-transform: capitalize;
  display: flex;
  justify-content: space-between;
  padding: 1em;
  font-weight: bold;
  font-size: 1.25rem;
}
.img-container {
  position: relative;
  overflow: hidden;
  cursor: pointer;
  width: 100%;
}
.store-img {
  transition: all 1s ease-in-out;
  width: 100%;
}
.img-container:hover .store-img {
  transform: scale(1.2);
}
.store-item-icon {
  position: absolute;
  bottom: 0;
  right: 0;
  padding: 0.75rem;
  background: var(--mainYellow);
  border-top-left-radius: 1rem;
  transition: all 1s ease-in-out;
  transform: translate(100%, 100%);
}
.img-container:hover .store-item-icon {
  transform: translate(0, 0);
}
.store-item-icon:hover {
  color: var(--mainWhite);
}
.search-input {
  max-width: 100%;
  margin: 0 auto;
  display: flex;
}
.search-input form,
.input-group {
  width: 100%;
  display: flex;
}
.input-group {
  padding: 2rem;
}
.input-group input {
  width: 100%;
  font-size: 1em;
  margin: 0;
  font-family: inherit;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  line-height: 1.5;
  color: #495057;
  border: 1px solid #ced4da;
  border-radius: 0.25rem;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  position: relative;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.search-box {
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  background-color: #e9ecef;
  border: 1px solid #ced4da;
  border-radius: 0.25rem;
  background: var(--mainPink);
  color: var(--mainBlack);
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

@media (min-width: 576px) {
  .store-items {
    grid-template-columns: repeat(1, 1fr);
  }
  .sortBtn,
  .search-input {
    width: 100%;
  }
  .sortBtn {
    justify-content: space-between;
  }
}

@media (min-width: 768px) {
  .store-items {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 992px) {
  .store-items {
    grid-template-columns: repeat(3, 1fr);
  }
  .sortBtn,
  .search-input {
    width: 80%;
  }
}
@media (min-width: 1200px) {
  .store-items {
    grid-template-columns: repeat(3, 1fr);
  }
  .sortBtn,
  .search-input {
    width: 60%;
  }
}

.cart-info__icon {
  color: var(--mainBlack);
  cursor: pointer;
}
.empty-cart {
  text-align: center;
  padding: 1rem;
}

.cart-info {
  border: 0.1rem solid var(--mainBlack);
  color: var(--mainBlack);
  border-radius: 0.25rem;
  padding: 0.4rem 0.6rem;
  cursor: pointer;
}
.cart-info:hover {
  background: var(--mainPink);
  border-color: var(--mainPink);
  color: var(--mainWhite);
}
.cart-info:hover .cart-info__icon {
  color: var(--mainWhite);
}
.cart {
  position: fixed;
  min-height: 10rem;
  background: var(--mainWhite);
  transition: all 0.3s ease-in-out;
  min-width: 300px;
  width: 24rem;
  padding: 1.5rem 0;
  transform: rotateY(-360deg);
  z-index: 1;
  top: 67px;
  right: 5px;
  box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12) !important;
}
.cart-item {
  transition: all 2s ease-in-out;
  padding: 1em 1.5rem;
  border-bottom: 1px solid #d4d4d4;
  display: flex;
  align-items: center;
  text-transform: capitalize;
}
.cart-actions {
  justify-items: flex-end;
  display: flex;
  box-sizing: border-box;
  font-weight: bold;
}
.cart-actions button {
  font-size: 1em;
  padding: 1em 0.6em;
  background-color: transparent;
}
.cart-actions input {
  border-right: none;
  border-left: none;
  width: 30px;
  padding: 1em 0;
  text-align: center;
}
.cart-item img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  padding-right: 1em;
}
.cart-item .item-text {
  display: block;
  padding: 0;
  font-size: 1rem;
  flex: 1 1 auto;
}
.cart-item .item-text p {
  margin: 0;
}
.cart-item:hover {
  background-color: #d4d4d4;
}
.cart-item-remove i.fas {
  margin: 0;
}
.cart-item-remove {
  color: var(--mainPink);
  transition: all 1s ease-in-out;
}

.cart-buttons-container {
  display: flex;
  padding: 1em;
  justify-content: space-between;
}
#cart-item-price {
  font-size: 0.8rem;
}
.cart-total-container {
  display: flex;
  justify-content: space-around;
  margin: 1rem;
  text-transform: capitalize;
  font-size: 1.25rem;
}
.btn-pink {
  color: var(--mainPink) !important;
  border-color: var(--mainPink) !important;
}
.btn-black {
  color: var(--mainBlack) !important;
  border-color: var(--mainBlack) !important;
}
.btn-black:hover,
.btn-black.active {
  color: var(--mainPink) !important;
  background: var(--mainBlack) !important;
}

.btn-pink:hover {
  background: var(--mainPink) !important;
  color: var(--mainBlack) !important;
}

ul {
  list-style: none;
}
.navbar {
  display: flex;
  padding: 0.5rem 1rem;
  align-items: center;
  position: fixed;
  z-index: 2;
  top: 0;
  width: 100%;
  background: var(--mainWhite);
  box-sizing: border-box;
  box-shadow: 0px 3px 1px 0px var(--mainGrey);
}
.mobile-navbar {
  flex-wrap: wrap;
  justify-content: space-between;
}
.nav-info {
  display: flex;
  justify-content: space-between;
  margin: 0 3rem;
  align-items: center;
}
.navbar-brand {
  display: inline-block;
  padding: 0.3125rem 0;
  margin-right: 1rem;
  font-size: 1.25rem;
  line-height: inherit;
}
i.fas {
  margin-right: 0.5rem;
}
.nav-info a {
  color: inherit;
}
.nav-info-items {
  display: flex;
  align-items: center;
}
.nav-info-items p {
  margin: 0;
}
.d-block {
  display: block !important;
  flex: 0 1 auto;
  padding: 0;
}

.navbar-collapse {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.navbar-nav {
  text-transform: capitalize;
  display: flex;
  margin: 0 auto;
}
.navbar-nav.column {
  flex-direction: column;
  padding: 0;
}
.toggler-icon {
  font-size: 2.5rem;
  color: var(--mainPink);
}
.nav-link {
  color: var(--mainPink);
  font-size: 1.5rem;
  transition: all 0.5s ease-in-out;
  padding: 0.5rem 1rem;
  display: block;
}
.nav-link:hover {
  color: var(--mainBlack);
}
.navbar-toggler {
  padding: 0.25rem 0.75rem;
  padding-right: 0;
  font-size: 1.25rem;
  line-height: 1;
  background-color: transparent;
  border: 1px solid transparent;
  border-radius: 0.25rem;
  outline: none !important;
  outline: none;
  cursor: pointer;
}

/* about */

.about {
  display: flex;
  flex-wrap: wrap;
  padding: 3rem 0;
}
.about > * {
  margin: 3rem 0;
  flex-basis: 100%;
}
.about-title {
  text-transform: capitalize;
}
.about-desc {
  width: 75%;
  margin: 2rem 0;
  color: #767474;
}
.about-img__container {
  position: relative;
}

.about-img__container::before {
  content: '';
  position: absolute;
  top: -1.5rem;
  left: -1.7rem;
  width: 100%;
  height: 100%;
  outline: 0.5rem solid var(--mainYellow);
  z-index: -1;
  transition: all 1s ease-in-out;
}
.about-img__container:hover:before {
  top: 0;
  left: 0;
}
.about-img__container img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
  border-style: none;
}

@media (min-width: 576px) {
  .about > * {
    flex-basis: 100%;
  }
}

@media (min-width: 768px) {
  .about > * {
    flex-basis: 100%;
  }
}

@media (min-width: 992px) {
  .about > * {
    flex-basis: 50%;
  }
}
@media (min-width: 1200px) {
  .about > * {
    flex-basis: 50%;
  }
}

